
* {
	margin: 0;
	padding: 0;
}
a {
	text-decoration: none;
	color: inherit;
}
ul,
li {
	list-style: none;
}
.information {
	margin-bottom: 18px;
}
.information > a {
	color: #E23150;
}
.information > a:hover {
	text-decoration: underline;
}
#app {
	font-family: 'Avenir', Helvetica, Arial, sans-serif, onsolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-align: center;
	color: #2c3e50;
	width: 520px;
	margin: 60px auto;
	-webkit-box-shadow: 5px 5px 25px 0 rgba(46, 61, 73, .2);
	        box-shadow: 5px 5px 25px 0 rgba(46, 61, 73, .2);
}
#app > h1 {
	padding: 20px 0;
	font-size: 50px;
}
#app > h1 > span {
	color: #E23150;
}
#logInAndSignUp {
	/*background: #FAFBFC;*/
	border-bottom-left-radius: 10px;
	border-bottom-right-radius: 10px;
	overflow: hidden;
}
#logInAndSignUp > .wrapper {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	width: 70%;
	margin: 0 auto;
}
#logInAndSignUp > .wrapper > .tab-title {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	font-size: 20px;
}
#logInAndSignUp > .wrapper > .tab-title > label {
	display: block;
	-webkit-box-flex: 1;
	    -ms-flex-positive: 1;
	        flex-grow: 1;
	text-align: center;
	padding: 20px;
	/*border-bottom: 1px solid #dbe2e8;*/
	/*background: #FAFBFC;*/
	cursor: pointer;
	line-height: 29px;
	-webkit-transition: all .5s;
	transition: all .5s;
}
#logInAndSignUp > .wrapper > .tab-title > label:hover {
	color: #E23150;
}
#logInAndSignUp > .wrapper > .tab-title > label.active {
	color: #E23150;
}
#logInAndSignUp > .wrapper > .tab-title > label.active:after {
	content: '';
	display: block;
	width: 100%;
	height: 2px;
	background: #E23150;
	margin-top: 6px;
}
#logInAndSignUp > .wrapper .formRow > input {
	margin: 8px;
	width: 322px;
	padding: 6px 10px;
	font-size: 16px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	-webkit-box-shadow: 0 0.25em 0.5em 0 rgba(46, 61, 73, .12);
	        box-shadow: 0 0.25em 0.5em 0 rgba(46, 61, 73, .12);
	-webkit-transition: border .3s ease, -webkit-box-shadow .3s ease;
	transition: border .3s ease, -webkit-box-shadow .3s ease;
	transition: box-shadow .3s ease, border .3s ease;
	transition: box-shadow .3s ease, border .3s ease, -webkit-box-shadow .3s ease;
	border: 1px solid #dbe2e8;
	outline: none;
}
#logInAndSignUp > .wrapper .formRow > input:hover {
	-webkit-box-shadow: 0 0.125em 0.5em 0 rgba(46, 61, 73, .06);
	        box-shadow: 0 0.125em 0.5em 0 rgba(46, 61, 73, .06);
}
#logInAndSignUp > .wrapper .formRow > input:focus {
	border: 1px solid rgba(226, 49, 80, 0.15);
	-webkit-box-shadow: 0 0 0.75em 0.25em rgba(226, 49, 80, 0.15);
	        box-shadow: 0 0 0.75em 0.25em rgba(226, 49, 80, 0.15);
}
#logInAndSignUp > .wrapper .formActions > input {
	margin: 8px 0 20px 0;
	width: 322px;
	padding: 6px 10px;
	font-size: 16px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	-webkit-box-shadow: 0 0.25em 0.5em 0 rgba(46, 61, 73, .12);
	        box-shadow: 0 0.25em 0.5em 0 rgba(46, 61, 73, .12);
	-webkit-transition: all .3s ease, border .3s ease;
	transition: all .3s ease, border .3s ease;
	border: 1px solid #dbe2e8;
	background: #E23150;
	color: white;
	outline: none;
}
#logInAndSignUp > .wrapper .formActions > input:hover {
	-webkit-box-shadow: 0 0.125em 0.5em 0 rgba(46, 61, 73, .06);
	        box-shadow: 0 0.125em 0.5em 0 rgba(46, 61, 73, .06);
	background: rgb(255, 55, 89);
}
#logInAndSignUp > .wrapper > div > label > input {
	display: none;
}

