#todo > ul > li {
	width: 322px;
	margin: 10px auto;
	padding: 10px 24px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	text-align: left;
	border-radius: 6px;
	position: relative;
	background: #f7f7f7;
}
#todo > ul > li:last-child {
	margin-bottom: 26px;
}
#todo > ul > li > .liItem {
	font-size: 26px;
	font-weight: bold;
}
#todo > ul > li > #checkbox {
	position: absolute;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	width: 30px;
	height: 30px;
	border-radius: 10px;
	right: 50px;
	background: white;
	-webkit-box-shadow: 0 0.25em 0.5em 0 rgba(46, 61, 73, .12);
	        box-shadow: 0 0.25em 0.5em 0 rgba(46, 61, 73, .12);
	-webkit-transition: all .3s;
	transition: all .3s;
	cursor: pointer;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	font-weight: bold;
	color: #8E9799;
}
#todo > ul > li > #checkbox:hover {
	-webkit-box-shadow: 0 0.125em 0.5em 0 rgba(46, 61, 73, .06);
	        box-shadow: 0 0.125em 0.5em 0 rgba(46, 61, 73, .06);
	color: #E23150;
}
#todo > ul > li.finished > #checkbox {
	color: white;
	background: #E23150;
	-webkit-box-shadow: 0 0.125em 0.5em 0 rgba(46, 61, 73, .06);
	        box-shadow: 0 0.125em 0.5em 0 rgba(46, 61, 73, .06);
}
#todo > ul > li > button {
	border: none;
	background: white;
	position: absolute;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	width: 32px;
	height: 32px;
	border-radius: 10px;
	right: 10px;
	font-size: 20px;
	-webkit-transition: all .3s;
	transition: all .3s;
	-webkit-box-shadow: 0 0.25em 0.5em 0 rgba(46, 61, 73, .12);
	        box-shadow: 0 0.25em 0.5em 0 rgba(46, 61, 73, .12);
	cursor: pointer;
	color: #8E9799;
	outline: none;
}
#todo > ul > li > button:hover {
	color: #E23150;
	-webkit-box-shadow: 0 0.125em 0.5em 0 rgba(46, 61, 73, .06);
	        box-shadow: 0 0.125em 0.5em 0 rgba(46, 61, 73, .06);
}
#todo > ul > li > .date {
	color: #8E9799;
	font-size: 14px;
}
